/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Kostas Bariotis' Blog","description":"I'm Kostas Bariotis, a web developer, a proud wanderer and a passionate doer. My mission is to write clean and efficient code, to solve problems on the web and to learn something more.","siteUrl":"https://kostasbariotis.com"}}},"pathContext":{"posts":[{"excerpt":"I started looking for a remote position, telecommuting in other words, as a software engineer last November. It took me 3 months to apply to ~150 job descriptions and none of them was a successful hire. Nevertheless, I now have a great job and I...","html":"<p>I started looking for a remote position, telecommuting in other words, as a software engineer last November. It took me 3 months to apply to ~150 job descriptions and none of them was a successful hire. Nevertheless, I now have a great job and I would like to share that experience with you. I have prepared a spreadsheet listing anonymous applications, whether they rejected me or I rejected them and till which level I got into. I will also share my cover letter and how I approached the remote job hunting.</p>\n<h2>Why a remote position?</h2>\n<p>After quite a few years doing this job, I have become quite selective when it comes to my next job. I need myself to be a good fit on the team and the team to be a good fit for me. I don't like big established corporations cause there is almost never anything interesting for me there. I like small ambitious teams, flexible enough to do one week sprints and put all of their heart in every sprint. I want the team's long-term goals to be my long term goals so I can put myself in it.</p>\n<p>I would call them \"startups\" just because it's cool nowadays, but I won't because that's simply not what I am all about. Startups are all about high risk, I am not (at least not at this point).</p>\n<p>While Greece had quite a few success stories of such teams, my hometown Thessaloniki, in northern Greece is not that famous for their startup environment. Leaving Greece wasn't possible at the moment so I thought I would chase a remote position especially after having lots of local friends who work for big companies like, GitHub, Automattic, etc.</p>\n<p>Let one not forget, there is also the compensation parameter that says that you can enter a better-paid market than your local market, which is no debatable for my local market.</p>\n<p>So I thought I would give it a try.</p>\n<h2>The setup</h2>\n<p>The setup when you do job hunting has everything to do with how you present yourself in the online world. How I present myself you ask? Well, you are looking at it. This web page contains everything you need to know about me, all the projects I am working on, everything I have share with my readers, lots of my views about stuff (ok not everything, but you get the idea). You can find all my social profiles (GitHub, LinkedIn, Twitter, etc) through this web page and lots of other places are linking back to my blog posts.</p>\n<p>But to make the recruiter to visit my page, I had to go through her mailbox first. So I tried to write a catchy email worthy enough to make her click my web page. Keep in mind that I am not targeting a big corporation but rather a team where the casual speech is usual. I wanted to write an email that will automatically filter out the \"serious\" guys and attract the more friendly ones. That's why the friendly tone.</p>\n<p>This is the final version of it because I had to do some revises after I sent it a few times.</p>\n<blockquote>\n<p>Dear Mr/Ms,</p>\n<p>I am Kostas Bariotis, a software developer and a passionate doer. I am sending you my regards from sunny Thessaloniki, Greece. Have you ever been? Take a look, I am sure you will fall in love with the amazing beaches of Chalkidiki.</p>\n<p>I am currently looking for a product and a team that I would be a great fit in. I stumbled upon your job description and I would really love it to talk more with you guys. I have been working with Node.js for the past three years. (<strong>This is where I explain why I would be a good fit for that particular position</strong>)</p>\n<p>I am obsessed with clean and efficient code. I am interested in designing software architectures, APIs and databases to serve the rest of an application. I am a full stack developer yet I love taking a specific field in my hands and getting the juice out of it. I am interested in being a part of a team that builds great software. I have spent the last two years with the <a href=\"https://goodvid.io\">Goodvid.io</a> team to serve e-commerce sites with curated product videos.</p>\n<p>My blog is my personal playground, I am also doing a lot of public speaking talking a lot about tech stuff and also talking with others on my podcast. Stargento and <a href=\"https://gityeller.com\">GitYeller</a> are two of my side projects. You can find more code samples and my contributions on <a href=\"https://github.com/kbariotis\">Github</a>. Along with some folks, we are organizing the local <a href=\"https://www.meetup.com/Thessaloniki-Node-js-Meetup/\">Nodejs meetup</a> and <a href=\"http://devitconf.org\">Devit Conf</a>, the biggest web development conference in northern Greece. We are also known as <a href=\"https://skgtech.io\">SKGTech</a>.</p>\n<p>Last, but not least, I love reading books, mainly science fiction, traveling and attending conferences and meetups, snowboarding and staying fit. Calisthenics class is my latest activity that takes a lot of my time.</p>\n<p>I would love it if we could arrange a talk and talk more about how I could potentially be a fit for your team. Please, consult my resume or see the links below.</p>\n<p>Thank you so much and have a nice day. :)</p>\n</blockquote>\n<h2>Where did I look?</h2>\n<p>Nowadays there are lots of places to find a remote position, especially in software engineering. Here's my list of preference:</p>\n<ul>\n<li><a href=\"https://weworkremotely.com/\">weworkremotely.com</a></li>\n<li><a href=\"https://stackoverflow.com/jobs?sort=i&#x26;r=true\">Stackoverflow Jobs</a> (with <code>Offers remote</code> filter on)</li>\n<li><a href=\"http://angel.co\">Angel.co</a> (mostly known for early stages companies)</li>\n<li><a href=\"https://github.com/jessicard/remote-jobs\">https://github.com/jessicard/remote-jobs</a></li>\n<li><a href=\"https://github.com/lukasz-madon/awesome-remote-job\">https://github.com/lukasz-madon/awesome-remote-job</a></li>\n</ul>\n<h2>The results</h2>\n<p>Like I said I didn't have a successful hire after all. Lots of companies showed interest in me and lots of others didn't even bother contacting me back.</p>\n<p><a href=\"https://docs.google.com/spreadsheets/d/17w6jiewdPQSgSL3Rripit2RdjWcR_I_m7ObBObmQAh0/edit?usp=sharing\">I have gathered over 70 applications</a> the months that I was looking for a job. Keep in mind that these are only those that I had some sort of communication through email or some other mean. I also did lots of applications on Angel.co and other similar sites that I've never heard back. The sum of the applications must exceed those 70 by far.</p>\n<h2>What I have learned</h2>\n<p>The progress I had with each company varies. It's interesting to see how many contacted me back and how many wanted to do an <a href=\"https://kostasbariotis.com/interviewer-vs-interviewee/\">interview</a> and how many interviews did I took on average with each company. I believe, though, that the most interesting part is that with over 70 applications I did, I took back like 3 offers which lead me to the conclusion that the market of the remote positions is highly competitive and it's really hard to find the job that you would be happy enough.</p>\n<p>As you enter a bigger market of potential employers, the same way they enter a bigger market of potential employees. Most of the times, the best yet the cheapest at their fields are those who get hired.</p>\n<p>But that's ok, cause this makes the market grow. There are still these companies that are looking for other parameters and don't care about the cost that much, but these companies have higher expectations and juniors/mid developers are often not being considered that easily.</p>\n<p>I am back on track now on a great team and an amazing environment in the heart of Thessaloniki. I am not rejecting the idea of working remotely entirely but I will leave it aside for now.</p>\n<p>Hope that you liked this post.</p>","id":"/Users/Bariotis/Dev/develop/kostasbariotis.com/src/pages/how-i-failed-landing-a-remote-position/index.md absPath of file >>> MarkdownRemark","timeToRead":5,"frontmatter":{"date":"2017-07-07T17:43:00.000Z","path":"/how-i-failed-landing-a-remote-position/","tags":"interviews, job, engineers","title":"How I failed landing a remote position","draft":null}},{"excerpt":"A few days ago I stumbled upon this wonderful  story . I love those short stories. They are real, inspirational and educational stories. I've also have two stories on my head that I kept saying when I am among fellows but I never wrote them somewhere...","html":"<p>A few days ago I stumbled upon this wonderful <a href=\"http://cs.txstate.edu/~br02/cs1428/ShortStoryForEngineers.htm\">story</a>. I love those short stories. They are real, inspirational and educational stories.</p>\n<p>I've also have two stories on my head that I kept saying when I am among fellows but I never wrote them somewhere. Here's my chance. Those stories are not mine but I have them in my mind for so long that I can't even remember where I read them. If you have any clue please share with me, in order to give credits to the original authors.</p>\n<p>Here's the first one.</p>\n<blockquote>\n<p>A train's engine got broken while in the road. It was recently purchaced and it was it's first trip and the president of the transportation company was on it. It happen also to be on, an engineer that was specialized on those kinds of engines. So the president headed to him and asked for help. He was desparate, afraid of the passengers to not left pleased. The engineer agreed to fix the engine and asked for 10.000$. The president took the deal even though the price was insane. The engineer headed to the front of the train. Picked up a hammer that was nearby and started looking at the engine. Everyone was waiting and suddenly he makes three hits to three different spots. Big hits. He yield to the driver to turn on the engine and for everyone's surprise the engine was running again. He fixed it. But the president was mad. He turned to the enginneer and said \"You charded me 10.000$ just to take three hits with the hammer? I would done it my self!\". And the engineer responded \"I didn't charged you for the hitting. I charged you because I knew... where to hit.\"</p>\n</blockquote>\n<p>Amazing. I am always carrying this story in my head. In fact, this story drove me where I am today, always trying to learn things that few people only know. I hope it inspires you too.</p>\n<p>And here's the second.</p>\n<blockquote>\n<p>A young junior employee on an private investements company did a serious, very critical mistake, that cost the company 1.000.000$. The man very sad, the next day went to his manager office and he handed his resignation. He said to him \"I know you probably going to fire me for the mistake I've done so I am quiting.\". And the manager said: \"Are you being serious? I just spend a million on your training. You aren't going anywhere!\".</p>\n</blockquote>\n<p>Loved this story too. Experience is so important. It's  what defines you as a professional. But in order to gain it you need to act. Without fear.</p>\n<p>And again, these stories are not mine and if you happen to know the original authors please share them with me.</p>\n<p>Do you know any good stories?</p>","id":"/Users/Bariotis/Dev/develop/kostasbariotis.com/src/pages/the-stories-in-my-head/index.md absPath of file >>> MarkdownRemark","timeToRead":2,"frontmatter":{"date":"2015-06-13T13:46:56.000Z","path":"/the-stories-in-my-head/","tags":"engineers, experience, stories","title":"The stories in my head","draft":null}}],"tag":"engineers","pagesSum":1,"page":1}}

/***/ })

